"use client";

import { useState, useEffect } from 'react';
import Calendar from '../components/Calendar';
import TaskModal from '../components/TaskModal';
import { useRouter } from 'next/navigation';

export default function Dashboard() {
  const [user, setUser] = useState(null);
  const [tasks, setTasks] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedTask, setSelectedTask] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();

  // Check if user is logged in
  useEffect(() => {
    const token = localStorage.getItem('token');
    const userData = localStorage.getItem('user');
    
    if (!token || !userData) {
      router.push('/');
      return;
    }
    
    try {
      setUser(JSON.parse(userData));
      fetchTasks(JSON.parse(userData).id);
    } catch (error) {
      console.error('Failed to parse user data:', error);
      router.push('/');
    } finally {
      setIsLoading(false);
    }
  }, [router]);

  // Fetch user tasks
  const fetchTasks = async (userId) => {
    if (!userId) return;
    
    try {
      setIsLoading(true);
      const response = await fetch(`http://localhost:5000/api/tasks/${userId}`);
      if (response.ok) {
        const data = await response.json();
        setTasks(data);
      } else {
        console.error('Failed to fetch tasks');
      }
    } catch (error) {
      console.error('Error fetching tasks:', error);
    } finally {
      setIsLoading(false);
    }
  };

  // Handle date click on calendar
  const handleDateClick = (date) => {
    setSelectedDate(date);
    setSelectedTask(null);
    setIsModalOpen(true);
  };

  // Handle task click for editing
  const handleTaskClick = (task) => {
    setSelectedTask(task);
    setSelectedDate(new Date(task.date));
    setIsModalOpen(true);
  };

  // Handle task save (create or update)
  const handleSaveTask = async (taskData) => {
    if (!user) return;

    try {
      const method = taskData._id ? 'PUT' : 'POST';
      const url = taskData._id 
        ? `http://localhost:5000/api/tasks/${taskData._id}`
        : 'http://localhost:5000/api/tasks';
      
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...taskData,
          user: user.id
        }),
      });

      if (response.ok) {
        fetchTasks(user.id);
      } else {
        console.error('Failed to save task');
      }
    } catch (error) {
      console.error('Error saving task:', error);
    }
  };

  // Handle task deletion
  const handleDeleteTask = async (taskId) => {
    if (!user || !taskId) return;

    try {
      const response = await fetch(`http://localhost:5000/api/tasks/${taskId}`, {
        method: 'DELETE',
      });

      if (response.ok) {
        fetchTasks(user.id);
      } else {
        console.error('Failed to delete task');
      }
    } catch (error) {
      console.error('Error deleting task:', error);
    }
  };

  // Handle logout
  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    router.push('/');
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <p>Loading...</p>
      </div>
    );
  }

  return (
    <div className="bg-[#fdf6e3] min-h-screen">
      <header className="bg-white shadow">
        <div className="max-w-7xl mx-auto py-4 px-4 sm:px-6 lg:px-8 flex justify-between items-center">
          <h1 className="text-2xl font-bold text-gray-900">Women in STEM UBC-Calendar</h1>
          {user && (
            <div className="flex items-center gap-4">
              <span>Welcome, {user.email}</span>
              <button
                onClick={handleLogout}
                className="bg-red-500 hover:bg-red-600 text-white py-1 px-3 rounded"
              >
                Logout
              </button>
            </div>
          )}
        </div>
      </header>

      <main className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
        <div className="px-4 py-6 sm:px-0">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Calendar Section */}
            <div className="lg:col-span-2">
              <Calendar 
                onDateClick={handleDateClick} 
                userId={user ? user.id : null}
              />
            </div>

            {/* Tasks Section */}
            <div className="bg-white shadow rounded-lg p-4">
              <h2 className="text-xl font-bold mb-4">Your Tasks</h2>
              {tasks.length === 0 ? (
                <p className="text-gray-500">No tasks yet. Click on a date to add one.</p>
              ) : (
                <ul className="space-y-2">
                  {tasks.map(task => (
                    <li 
                      key={task._id}
                      className="border rounded p-3 hover:bg-gray-50 cursor-pointer"
                      onClick={() => handleTaskClick(task)}
                    >
                      <div className="flex justify-between">
                        <h3 className="font-medium">{task.title}</h3>
                        <button 
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDeleteTask(task._id);
                          }}
                          className="text-red-500 hover:text-red-700"
                        >
                          Delete
                        </button>
                      </div>
                      <p className="text-gray-600 text-sm">{new Date(task.date).toLocaleDateString()}</p>
                      {task.description && (
                        <p className="text-gray-500 mt-1 text-sm">{task.description}</p>
                      )}
                    </li>
                  ))}
                </ul>
              )}
              
              <button
                onClick={() => handleDateClick(new Date())}
                className="mt-4 w-full bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
              >
                Add New Task
              </button>
            </div>
          </div>

          {/* Google Calendar Auth Button */}
          <div className="mt-6 bg-white shadow rounded-lg p-4">
            <h2 className="text-xl font-bold mb-4">Connect with Google Calendar</h2>
            <p className="mb-4">
              Connect your Google Calendar to see all your events in one place.
            </p>
            <a
              href="http://localhost:5000/auth/google/url"
              className="inline-block bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
            >
              Connect Google Calendar
            </a>
          </div>
        </div>
      </main>

      {/* Task Modal */}
      <TaskModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSave={handleSaveTask}
        selectedDate={selectedDate}
        task={selectedTask}
      />
    </div>
  );
}